// 
const transformTime = new Date() || function() {
	if (timestamp) {
		var time = new Date(timestamp);
		var y = time.getFullYear(); //getFullYear
		var M = time.getMonth() + 1; // getMonth Date  (0 ~ 11)
		var d = time.getDate(); // getDate Date  (1 ~ 31)
		var h = time.getHours(); // getHours Date  (0 ~ 23)
		var m = time.getMinutes(); // getMinutes Date  (0 ~ 59)
		var s = time.getSeconds(); // getSeconds Date  (0 ~ 59)
		return y + '-' + M + '-' + d + ' ' + h + ':' + m + ':' + s;
	} else {
		return '';
	}
}

// 
const getQueryString = function (url, name) {
  console.log("url = " + url)
  console.log("name = " + name)
  var reg = new RegExp('(^|&|/?)' + name + '=([^&|/?]*)(&|/?|$)', 'i')
  var r = url.substr(1).match(reg)
  if (r != null) {
    console.log("r = " + r)
    console.log("r[2] = " + r[2])
    return r[2]
  }
  return null;
}

//  null => ''
const null2str = function(res) {
	for (let x in res) {
		if (res[x] === null) { // null  ''
			res[x] = ''
		} else {
			if (Array.isArray(res[x])) { //  
				res[x] = res[x].map(z => {
					return this.null2str(z)
				})
			}
			if (typeof(res[x]) === 'object') { // json 
				res[x] = this.null2str(res[x])
			}
		}
	}
	return res
}

// 
const now = Date.now || function() {
	return new Date().getTime();
};

// 
const isArray = Array.isArray || function(obj) {
	return obj instanceof Array;
};

export default {
	requestUrl,
	imgUrl,
	transformTime,
	null2str,
	now,
	isArray,
	getQueryString
}
